@import "sass/index";

@keyframes linesAnimate {
  0% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}

@keyframes meteorAnimate {
  0% {
    opacity: 0;
    transform: translate(0%, 0%);
  }

  20% {
    opacity: 0;
    transform: translate(-15%, 10%);
  }

  39% {
    opacity: 0;
  }

  40% {
    opacity: 0.5;
    transform: translate(-15%, 10%);
  }

  75% {
    opacity: 1;
    transform: translate(-10%, 10%);
  }

  80% {
    opacity: 0;
    transform: translate(-10%, 10%);
  }

  85% {
    opacity: 0.8;
    transform: translate(-15%, 0%);
  }

  87% {
    opacity: 0.6;
    transform: translate(0%, 5%);
  }

  79% {
    opacity: 0.5;
    transform: translate(-25%, 15%);
  }

  83% {
    transform: translate(-5%, 0%);
  }

  86% {
    opacity: 0.7;
    transform: translate(-20%, -10%);
  }

  89% {
    transform: translate(0%, 10%);
  }

  92% {
    opacity: 1;
    transform: translate(20%, -5%);
  }

  95% {
    opacity: 0.6;
    transform: translate(-15%, 15%);
  }

  97% {
    transform: translate(10%, 0%);
  }

  100% {
    opacity: 1;
    transform: translate(0%, -20%);
  }
}

@keyframes cloudAnimate {
  0% {
    transform: translate(-700%, 289%) scale(3);
    opacity: 0;
  }

  40% {
    transform: translate(-150%, 80%) scale(1.3);
    opacity: 0.8;
  }

  50% {
    transform: translate(-60%, 25%) scale(1);
    opacity: 1;
  }

  60% {
    transform: translate(-60%, -35%) scale(1.5);
    opacity: 0.7;
  }

  100% {
    transform: translate(-160%, -220%) scale(2);
    opacity: 0;
  }
}

@keyframes cloud_1Animate {
  0% {
    transform: translate(-500%, 600%) scale(3);
    opacity: 0;
  }

  23% {
    transform: translate(-180%, 400%) scale(2);
    opacity: 0.8;
  }

  50% {
    transform: translate(-13%, 40%) scale(1);
    opacity: 1;
  }

  100% {
    transform: translate(110%, 265%) scale(2);
    opacity: 0;
  }
}

@keyframes ballWrapperAnimate {
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(3%);
  }
}

.ballWrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  animation: ballWrapperAnimate 2s steps(35, end) infinite alternate;

  & {
    .ballHead {
      position: absolute;
      top: -12%;
      right: 5%;
      height: 81% !important;
      width: auto !important;
    }

    .lines {
      position: absolute;
      right: 0;
      top: 0;

      height: 100% !important;
      width: auto !important;
      animation: linesAnimate 1.5s steps(10, end) infinite alternate;
    }
  }
}

.meteor {
  position: absolute;
  right: 32%;
  top: 43%;

  height: auto !important;
  width: 5% !important;

  animation: meteorAnimate 5s steps(5, end) infinite alternate;
}

.cloud {
  position: absolute;
  right: 30%;
  top: 48%;

  height: auto !important;
  width: 3% !important;

  animation: cloudAnimate 5s steps(40, end) infinite;
}
.cloud_1 {
  position: absolute;
  right: 21.5%;
  top: 71%;
  height: auto !important;
  width: 6% !important;

  animation: cloud_1Animate 5s steps(40, end) infinite;
}

.ballLine {
  position: absolute;
  left: 0%;
  bottom: 0%;

  height: auto !important;
  width: 80% !important;
}
